@use '../base/variables' as v;
@use '../base/mixins' as m;

.header{
    background-color: v.$negro; 
   
        @include m.tablet{
            &-fijo{
                position: fixed;
                width: 100%;
                box-shadow: .1rem .2rem .3rem #333;
            }
        }
    
    h1{
        text-transform: uppercase;
        color:v.$blanco;
        letter-spacing: 2px;
        text-align: center;
        @include m.tablet{
            margin: 0;    
        }
        
    }
    .contenido-header{
        padding: 2rem;
        @include m.tablet{
            display:flex;
            justify-content: space-between;
            align-items: center;
        
        }
    }
}
.navegacion-principal{
    @include m.tablet{
        display:flex;
        gap:2rem;
    }

    a{
        display: block;
        text-align: center;
        margin-bottom: 1rem;
        @include m.tablet{
            margin-bottom:0;
        }
        color:v.$blanco;
        font-size: 2.4rem;

        &:hover{
            color:v.$amarillo;
        }

    }
    
}
